import $uni from '@/utils/uni/api.js';

export default {
	props: {
		count: Number
	},

	methods: {
		async baseUpload() {
			const opts = {
					count: this.count
				},
				uploadRetObj = await $uni.media.img.upload(opts);

			this.$emit('upload', uploadRetObj);
		}
	}
};
